import React from "react"
import presets from "../utils/presets"
const Container = ({children}) => (
  <div css={{
    margin: `0rem auto`,
    paddingTop: `4.3rem`,
    paddingBottom: `4rem`,
    [presets.Tablet]: {
      paddingBottom: `1rem`
    },
    width: '96%',
    maxWidth: `960px`,
  }} >{children}</div>
);
export default Container;



